<script lang='ts'>
	export let props
	let reset = false
	let isAnimDone = false
	function logoClick() {
		if (isAnimDone) {
			reset = true
			isAnimDone = false
		}
	}

	function animStart() {
		isAnimDone = false
	}
	function animEnd() {
		isAnimDone = true
		if (reset) {
			reset = false
			isAnimDone = false
		}
	}
</script>

